// Store Table
import React from 'react';
import { connect } from 'react-redux';
import { Config } from '../constants/Config';

interface Props {
    currentPlayerPoints: any,
    isPlaying: boolean,
    matchNumber: number
}

const ScoreTable = (props: Props) => {
    const { currentPlayerPoints, isPlaying, matchNumber } = props;
    if (!isPlaying) return null;

    return (
        <div id="score-table">
            <div className="text-center">GAME: {matchNumber}</div>
            <div>
                <div>A: <span className="text-30">{currentPlayerPoints[Config.Player.A]}</span> points</div>
                <div>B: <span className="text-30">{currentPlayerPoints[Config.Player.B]}</span> points</div>
                <div>C: <span className="text-30">{currentPlayerPoints[Config.Player.C]}</span> points</div>
                <div>D: <span className="text-30">{currentPlayerPoints[Config.Player.D]}</span> points</div>
            </div>
        </div>
    )
}

const mapStateToProps = (state: any) => {
    return {
        isPlaying: state.game.isPlaying,
        currentPlayerPoints: Object.assign({}, state.game.playerPoints),
        matchNumber: state.game.matchNumber
    }
}

export default connect(mapStateToProps)(ScoreTable);